<template>
  <div class="operation">
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      fixed
      @click-left="onClickLeft"
    />
    <section class="van-doc-demo-section">

      <van-tabs v-model="active" swipeable animated>
        <van-tab title="标签 1">内容 1</van-tab>
        <van-tab title="标签 2">内容 2</van-tab>
        <van-tab title="标签 3">内容 3</van-tab>
      </van-tabs>

    </section>

  </div>
</template>

<script>
  export default {
    name: "operation",
    data() {
      return {
        title: '运维管理',
        active: 0
      }
    },
    methods: {
      onClickLeft() {
        this.$router.back();
      }
    }
  }
</script>

<style scoped>

</style>



